.header {
    margin-top: 5rem;
    height: 20rem;
    overflow: hidden;
    border-bottom: 2px solid var(--color-gray-400);
}

@media screen and (max-width: 1024px) {
    .header {
        height: fit-content;
    }
}

.header__container{
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    place-items: center;
    background: black;
}

.header__container-bg {
    position: absolute;
    width: 100%;
}

.header__container-bg img {
    opacity: 0.5;
}

.header__content {
    position: relative;
    width: 44%;
    margin: 0 auto;
    text-align: center;
    color: var(--color-gray-100);
}

@media screen and (max-width: 1024px) {
    .header__content {
        width: 76%;
    }
}

@media screen and (max-width: 600px) {
    .header__content {
        width: var(--container-width-md);
        padding: 3rem 0;
    }
}

.header__content  h2 {
    margin-bottom: 1rem;
}

.header__content p {
    color: rgba(255,255,255,0.7);
}

@media screen and (max-width: 600px) {
    .header__content p  {
        font-size: 0.85;
    }
}